import React from 'react';
import { Box, Container, Grid, makeStyles, Theme } from '@material-ui/core';
import { generateLogger } from 'utils/loggers';
import DrawerMenu from './DrawerMenu';
import { globalMenus } from 'models/menu';
import { Link } from 'gatsby';

const logger = generateLogger('global_header');
const handleMenuClick = (name: string) => () => {
  logger.click(`click_menu`, { name });
};
const handleDrawerStateChange = (open: boolean) => {
  if (open) {
    logger.impression('open_global_menu');
  } else {
    logger.impression('close_global_menu');
  }
};

const useStyles = makeStyles<Theme>(({ spacing, palette, breakpoints }) => ({
  root: {
    position: 'fixed',
    backgroundColor: 'rgba(255, 255, 255, 0.85)',
    width: '100%',
    left: '50%',
    transform: 'translateX(-50%)',
    zIndex: 300,
    backdropFilter: 'blur(3px)',
    paddingTop: spacing(1),
    height: 48,
  },
  drawer: {
    [breakpoints.up('md')]: {
      display: 'none',
    },
  },
  menu: {
    [breakpoints.down('sm')]: {
      display: 'none',
    },
    margin: 0,
    padding: 0,
    '& li': {
      margin: 0,
      listStyle: 'none',
      marginRight: spacing(2),
    },
    '& a': {
      textDecoration: 'none',
      color: 'inherit',
      '&.active': {
        color: palette.primary.main,
      },
      '&:hover': {
        textDecoration: 'underline',
      },
      '&:visited': {
        color: 'inherit',
      },
      fontFamily: `"Nanum Myeongjo", serif`,
    },
  },
}));

const Header = () => {
  const styles = useStyles();

  return (
    <Container component="header" className={styles.root}>
      <Box position="relative" height="100%" display="flex">
        <Grid container alignItems="center" justify="space-between">
          <Grid>
            <span className={styles.drawer}>
              <DrawerMenu onChange={handleDrawerStateChange} onClickMenu={handleMenuClick} />
            </span>
          </Grid>
          <Grid component="ul" item alignItems="center" xs={4} container className={styles.menu} justify="flex-end">
            {globalMenus.map(menu => (
              <Grid component="li" item key={menu.path}>
                <Link to={menu.path} onClick={handleMenuClick(menu.name)}>
                  {menu.name}
                </Link>
              </Grid>
            ))}
          </Grid>
        </Grid>
      </Box>
    </Container>
  );
};

export default Header;
