import React, { useState, useCallback, useMemo } from 'react';
import {
  SwipeableDrawer,
  List,
  ListItem,
  ListItemText,
  IconButton,
  useMediaQuery,
  useTheme,
  makeStyles,
  Theme,
} from '@material-ui/core';
import { globalMenus } from 'models/menu';
import * as Icon from 'react-feather';
import { Link } from 'gatsby';

const useStyles = makeStyles<Theme>(({ palette, spacing, typography }) => ({
  list: {
    margin: `${spacing(2)}px ${spacing(1)}px ${spacing(1)}px`,
    minWidth: 250,
    '& a': {
      textDecoration: 'none',
      color: palette.text.primary,
    },
    '& a span': {
      fontFamily: `"Nanum Myeongjo", serif`,
      fontSize: typography.body2.fontSize,
    },
  },
}));

interface Props {
  onChange?: (state: boolean) => void;
  onClickMenu?: (menuName: string) => void;
  onClickExternalLink?: (target: string) => void;
}
const DrawerMenu = ({ onClickMenu, onChange }: Props) => {
  const styles = useStyles();
  const [open, setOpen] = useState(false);
  const { breakpoints } = useTheme();
  const matches = useMediaQuery(breakpoints.up('lg'));
  const iconSize = useMemo(() => (matches ? 30 : 20), [matches]);

  const handleClick = useCallback(() => {
    setOpen(state => !state);
    onChange?.(true);
  }, [setOpen, onChange]);

  return (
    <>
      <IconButton onClick={handleClick} aria-label="menu">
        <Icon.Menu strokeWidth={1} size={iconSize} />
      </IconButton>
      <SwipeableDrawer
        open={open}
        onClose={() => {
          setOpen(false);
          onChange?.(false);
        }}
        onOpen={() => {
          setOpen(true);
          onChange?.(true);
        }}
      >
        <List className={styles.list}>
          {globalMenus.map(menu => (
            <li>
              <Link key={menu.path} to={menu.path} onClick={() => onClickMenu?.(menu.name)}>
                <ListItem button key={menu.path}>
                  <ListItemText>{menu.name}</ListItemText>
                </ListItem>
              </Link>
            </li>
          ))}
        </List>
      </SwipeableDrawer>
    </>
  );
};

export default DrawerMenu;
